/* eslint-disable camelcase */
import API_ENDPOINT from '../global/api-endpoint';

class AppSource {
  static async _execute(endpoint, options) {
    try {
      const response = await fetch(endpoint, options);
      const responseJson = await response.json();
      return responseJson;
    } catch (error) {
      const data = {
        error: true,
        data: error,
      };
      return data;
    }
  }

  static getPayment(data) {
    const options = {
      method: 'GET',
      headers: { Authorization: `${data.token}` },
    };
    return this._execute(API_ENDPOINT.FILTER_PAYMENTS(data), options);
  }

  static createPaymentRequest({ token, nim, nominal }) {
    const options = {
      method: 'POST',
      headers: {
        Authorization: `${token}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ nim, nominal }),
    };
    return this._execute(API_ENDPOINT.REQ_PAYMENT, options);
  }
}

export default AppSource;
